import Vue from 'vue'
if (!Array.prototype.remove) {
  Array.prototype.remove = function (item) {
    if (!this.length) return
    var index = this.indexOf(item)
    if (index > -1) {
      this.splice(index, 1)
      return this
    }
  }
}
var init = {
  lazyLoad: false,
  default: 'http://xplus-uploads-test.oss-cn-shenzhen.aliyuncs.com/tiger/avatar/2018/1010/17/5bbdc5cd3aa98.jpeg!330xauto'
}

var listenList = []
var imageCatcheList = []

const isAlredyLoad = (imageSrc) => {
  if (imageCatcheList.indexOf(imageSrc) > -1) {
    return true
  } else {
    return false
  }
}
// 
const isCanShow = (item) => {
  var ele = item.ele
  var src = item.src
  // 
  var top = ele.getBoundingClientRect().top
  // 
  var windowHeight = window.innerHight
  // top + 10 10
  if (top + 10 < window.innerHeight) {
    var image = new Image()
    image.src = src
    image.onload = function () {
      ele.src = src
      imageCatcheList.push(src)
      listenList.remove(item)
    }
    return true
  } else {
    return false
  }
}

const onListenScroll = () => {
  window.addEventListener('scroll', function () {
    var length = listenList.length
    for (let i = 0; i < length; i++) {
      isCanShow(listenList[i])
    }
  })
}
// Vue 
const addListener = (ele, binding, vnode) => {
  // 
  var imageSrc = vnode.data.attrs.src
  // src
  if (isAlredyLoad(imageSrc)) {
    ele.src = imageSrc
    return false
  }
  var item = {
    ele: ele,
    src: imageSrc
  }
  // 
  ele.src = init.default
  // 
  if (isCanShow(item)) {
    return
  }
  // lisenList
  listenList.push(item)
  // scroll
  onListenScroll()
}

Vue.directive('lazyload', {
  inserted: addListener,
  updated: addListener
})