/**
 * 
 */
class Browser {
  _navigator = navigator
  _userAgent = this._navigator.userAgent
  _language = (this._navigator.browserLanguage || this._navigator.language).toLowerCase()
  // appVersion = this.navigator.appVersion
  _version = this.getVersion()

  /**
   * 
   * @return {Object}
   */
  getVersion () {
    return {
      trident: this._userAgent.indexOf('Trident') > -1, // IE
      presto: this._userAgent.indexOf('Presto') > -1, // opera
      webKit: this._userAgent.indexOf('AppleWebKit') > -1, // 
      gecko: this._userAgent.indexOf('Gecko') > -1 && this._userAgent.indexOf('KHTML') === -1, // 
      mobile: !!this._userAgent.match(/AppleWebKit.*Mobile.*/), // 
      ios: !!this._userAgent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), // ios
      android: this._userAgent.indexOf('Android') > -1 || this._userAgent.indexOf('Linux') > -1, // androiduc
      iPhone: this._userAgent.indexOf('iPhone') > -1, // iPhoneQQHD
      iPad: this._userAgent.indexOf('iPad') > -1, // iPad
      wechat: this._userAgent.indexOf('MicroMessenger') > -1, // 
      webApp: this._userAgent.indexOf('Safari') === -1 // web
    }
  }

  /**
   * 
   * @return {boolean|*}
   */
  isMobile () {
    return this._version.mobile
  }

  /**
   * ios
   * @return {boolean|*}
   */
  isIos () {
    return this._version.ios
  }

  /**
   * android
   * @return {boolean|*}
   */
  isAndroid () {
    return this._version.android
  }

  /**
   * 
   * @return {boolean|*}
   */
  isWechat () {
    return this._version.wechat
  }

  /**
   * 
   * @return {string}
   */
  getLanguage () {
    return this._language
  }
}

export default new Browser()
