// jssdk mixin
import { getWechatSignApi } from '@/store/api/common'
import browser from '@/utils/browser'
import Vue from 'vue'
export default {
  data () {
    return {
      wechatConfig: {
        debug: false,
        jsApiList: [
          'updateAppMessageShareData',
          'updateTimelineShareData',
          'onMenuShareTimeline',
          'onMenuShareAppMessage',
          'startRecord',
          'stopRecord',
          'onVoiceRecordEnd',
          'playVoice',
          'pauseVoice',
          'stopVoice',
          'onVoicePlayEnd',
          'uploadVoice',
          'downloadVoice',
          'chooseImage',
          'previewImage',
          'uploadImage',
          'downloadImage',
          'chooseWXPay',
          'getLocalImgData',
          'openLocation',
          'getLocation'
        ]
      }
    }
  },
  methods: {
    /**
     * 
     */
    async getWechatSign () {
      const params = {
        url: location.href.split('#')[0],
        jsApiList: this.wechatConfig.jsApiList
      }
      const res = await getWechatSignApi(params)
      this.wechatConfig = Object.assign({}, res.data)
      this.setWechatConfig()
    },
    /**
     * sdk
     */
    setWechatConfig () {
      let wx = Vue.wx
      wx.config(this.wechatConfig)
      wx.ready(() => {
      })
      wx.error((res) => {
        console.log(res, '')
        // configerrorconfigdebugresSPA
      })
    },

    /**
     * 
     * @param {*} options
     */
    wechatChooseImage (options = {}) {
      return new Promise((resolve, reject) => {
        Vue.wx.chooseImage({
          count: options.count || 9, // 9
          sizeType: options.sizeType || ['original', 'compressed'], // 
          sourceType: options.sourceType || ['album', 'camera'], // 
          success: async function (res) {
            resolve(res.localIds)
          },
          fail: function (e) {
            reject(e)
          }
        })
      })
    },

    /**
     * base64
     * @param {*} localId
     */
    wechatGetLocalImgData (localId) {
      return new Promise((resolve, reject) => {
        if (browser.isIos() && Vue.wx.getLocalImgData) {
          Vue.wx.getLocalImgData({
            localId: localId, // localID
            success: function (res) {
              resolve(res)
            },
            fail: function (e) {
              reject(e)
            }
          })
        } else {
          resolve({ localData: '' })
        }
      })
    },

    /**
     * 
     * @param {*} options
     */
    wechatPreviewImage (options = {}) {
      return new Promise((resolve, reject) => {
        Vue.wx.previewImage({
          current: options.img,
          urls: options.urls
        })
      })
    },

    /**
     * 
     * @param {*} localId IDchooseImage
     * @param {*} options
     */
    wechatUploadImage (localId, options = {}) {
      return new Promise((resolve, reject) => {
        Vue.wx.uploadImage({
          localId: localId,
          isShowProgressTips: options.isShowProgressTips || 0, // sdk1
          success: function (res) {
            resolve(res)
          },
          fail: function (e) {
            reject(e)
          }
        })
      })
    },

    /**
     * 
     * @param {*} localId
     * @param {*} options
     */
    wechatUploadVoice (localId, options = {}) {
      return new Promise((resolve, reject) => {
        Vue.wx.uploadVoice({
          localId: localId, // IDstopRecord
          isShowProgressTips: options.isShowProgressTips || 1, // 1
          success: function (res) {
            // const serverId = res.serverId // ID
            resolve(res)
          },
          fail: function (e) {
            reject(e)
          }
        })
      })
    },

    /**
     * 
     * @return {Object}
     */
    getWechatRecorder () {
      const self = this
      let _localId = ''
      return {
        /**
         * 
         * @param {*} callback
         */
        triggerCallback (callback, ...options) {
          if (this[callback] && this[callback] instanceof Function) {
            this[callback](...options)
          }
        },

        /**
         * 
         * @return {Promise}
         */
        startRecord () {
          Vue.wx.startRecord({
            success: (res) => {
              this.triggerCallback('onStartRecord', res)
              Vue.wx.onVoiceRecordEnd({
                //  complete 
                complete: res => {
                  _localId = res.localId
                  this.triggerCallback('onRecordEnded', res)
                }
              })
            }
          })
        },

        /**
         * 
         * @return {Promise}
         */
        stopRecord ({ callStopRecord = true } = {}) {
          Vue.wx.stopRecord({
            success: res => {
              if (callStopRecord) {
                _localId = res.localId
                this.triggerCallback('onStopRecord', res)
              }
            }
          })
        },

        /**
         * 
         * @param {*} localId
         */
        playVoice ({localId} = {}) {
          localId = localId || _localId
          if (!localId) {
            return
          }
          Vue.wx.playVoice({
            localId: localId, // IDstopRecord
            success: () => {
              this.triggerCallback('onPlayVoice')
            }
          })
          Vue.wx.onVoicePlayEnd({
            success: res => {
              // var localId = res.localId // ID
              this.triggerCallback('onPlayVoiceEnded', res)
            }
          })
        },

        /**
         * 
         * @param {*} localId
         */
        pauseVoice ({localId} = {}) {
          localId = localId || _localId
          if (!localId) {
            console.log('localId')
            return
          }
          Vue.wx.pauseVoice({
            localId: localId, // IDstopRecord
            success: () => {
              this.triggerCallback('onPauseVoice')
            }
          })
        },

        /**
         * 
         * @param {*} localId
         * @param {*} callStopVoice 
         */
        stopVoice ({localId, callStopVoice = true} = {}) {
          localId = localId || _localId
          if (!localId) {
            console.log('localId')
            return
          }
          Vue.wx.stopVoice({
            localId: localId, // IDstopRecord
            success: () => {
              if (callStopVoice) {
                this.triggerCallback('onStopVoice')
              }
            }
          })
        }
      }
    },

    getWxLocation () {
      let wx = Vue.wx
      wx.ready(() => {
        wx.getLocation({
          type: 'wgs84', // wgs84gpsopenLocation'gcj02'
          success: function (res) {
            var latitude = res.latitude; // 90 ~ -90
            var longitude = res.longitude; // 180 ~ -180
            var speed = res.speed; // /
            var accuracy = res.accuracy; // 
            return res
          },
          cancel: function (e) {
            console.log(e)
          },
          error: function (e0) {
            console.log(e0)
          }
        })
      })
    },
    /**
     * 
     * @return {Object}
     */
    wxUploadFile ({mediaId, type, duration}) {
      return new Promise((resolve, reject) => {
        let data = {
          attach_id: mediaId,
          attach_type: type,
          duration: duration || 0
        }
        wxUploadFileApi(data).then(res => {
          resolve(res)
        })
      })
    },
    /**
     * 
     */
    async wechatShare (enter) {
      let wx = Vue.wx
      // 
      //  http 
      wx.ready(() => {

        // 
        wx.updateTimelineShareData({
          title: enter.title, // 
          link: enter.link,
          imgUrl: enter.imgUrl
        })
        // 
        wx.updateAppMessageShareData({
          title: enter.titles ? enter.titles : enter.title, // 
          desc: enter.desc,
          link: enter.link,
          imgUrl: enter.imgUrl
        })
      })
    }
  },
  created () {
    let wx = Vue.wx
    this.getWechatSign()
    wx.checkJsApi({
      jsApiList: this.wechatConfig.jsApiList, // JSJS2,
      success: function (res) {
        console.log('', res)
        // apitruefalse
        // {"checkResult":{"chooseImage":true},"errMsg":"checkJsApi:ok"}
      }
    })
  }
}
